import {
  Breadcrumb,
  Statistic,
  Flex,
  Layout,
  Typography,
  Table,
  Button,
  Popconfirm,
  type TableProps,
  Card,
  Skeleton,
  App,
} from 'antd';
import { useNavigate } from 'react-router';
import { useMutation, useQuery } from '@tanstack/react-query';
import dayjs from 'dayjs';
import SideBar from '../../components/SideBar/SideBar.tsx';
import {
  fetchAccessKeys,
  fetchAccount,
  logout,
  revokeAccessKey,
} from '../../api/client.ts';
import ContentFooter from '../../components/Footer/Footer.tsx';
import { AxiosError, AxiosResponse } from 'axios';
import { AddAccessKey } from './AddAccessKey.tsx';
import { useState } from 'react';

const { Title } = Typography;
const { Content } = Layout;

type AccessKey = {
  description: string;
  friendlyName: string;
  expires: number;
  id: string;
};

const Settings = () => {
  const navigate = useNavigate();
  const { message } = App.useApp();
  const [isAddNewModal, setAddNewModal] = useState<boolean>(false);

  const { isPending, data } = useQuery({
    queryKey: ['account'],
    queryFn: fetchAccount,
  });

  const {
    isPending: isPendingAccessKeys,
    data: accessKeysData,
    refetch,
  } = useQuery({
    queryKey: ['accessKeys'],
    queryFn: fetchAccessKeys,
  });

  const { mutate, isPending: revokePending } = useMutation<
    AxiosResponse,
    AxiosError,
    string
  >({
    mutationFn: revokeAccessKey,
    onSuccess: () => refetch(),
    onError: (error) => {
      message.open({
        type: 'error',
        content: (error?.response?.data as string) || "Couldn't create new app",
      });
    },
  });

  const { mutate: logoutMutation } = useMutation<AxiosResponse, AxiosError>({
    mutationFn: logout,
    onSuccess: () => {
      navigate('/login');
    },
    onError: (error) => {
      message.open({
        type: 'error',
        content: (error?.response?.data as string) || 'Logout error',
      });
    },
  });

  const breadcrumb = [
    {
      title: 'Home',
      onClick: () => navigate('/'),
    },
    {
      title: 'Settings',
    },
  ];

  const columns: TableProps<AccessKey>['columns'] = [
    {
      title: 'Name',
      dataIndex: 'friendlyName',
      key: 'friendlyName',
      render: (friendlyName) => friendlyName,
    },
    {
      title: 'Expires date',
      dataIndex: 'expires',
      key: 'expires',
      render: (expires) => dayjs(expires).format('MMM D, hh:mm A'),
    },
    {
      title: 'Actions',
      key: 'actions',
      align: 'center',
      render: (_, record) => (
        <Popconfirm
          title="Revoke key"
          description="Are you sure to revoke this key?"
          onConfirm={() => mutate(record.friendlyName)}
          okText="Yes"
          cancelText="No"
        >
          <Button color="danger" variant="link">
            Revoke
          </Button>
        </Popconfirm>
      ),
    },
  ];

  return (
    <SideBar>
      <Layout>
        <Content
          style={{
            paddingRight: 50,
            paddingTop: 20,
          }}
        >
          <Breadcrumb separator=">" items={breadcrumb} />
          <Flex align="center" justify="space-between">
            <Title level={2}>Profile</Title>
            <Button
              onClick={() => logoutMutation()}
              color="danger"
              variant="solid"
            >
              Logout
            </Button>
          </Flex>

          <Card loading={isPending}>
            <Statistic title="Account email:" value={data?.account?.email} />
            <br />
            <Statistic title="Account name:" value={data?.account?.name} />
            <br />
            <Statistic
              title="Auth providers:"
              value={data?.account?.linkedProviders?.join(',')}
            />
          </Card>

          <Flex align="center" justify="space-between">
            <Title level={2}>Access keys</Title>
            <Button onClick={() => setAddNewModal(true)} type="primary">
              Add new key
            </Button>
          </Flex>

          <Skeleton loading={revokePending}>
            {accessKeysData?.accessKeys && (
              <Table
                rowKey="friendlyName"
                loading={revokePending || isPendingAccessKeys}
                columns={columns}
                dataSource={accessKeysData?.accessKeys}
                pagination={false}
              />
            )}
          </Skeleton>
        </Content>
      </Layout>
      <AddAccessKey
        isOpen={isAddNewModal}
        onCancel={() => setAddNewModal(false)}
        onSuccess={() => {
          refetch();
          setAddNewModal(false);
        }}
      />
      <ContentFooter />
    </SideBar>
  );
};

export default Settings;
