import { type FC, useState } from 'react';
import { AxiosError, AxiosResponse } from 'axios';
import {
  App,
  Form,
  type FormProps,
  Input,
  InputNumber,
  Modal,
  Typography,
} from 'antd';
import { useMutation } from '@tanstack/react-query';
import dayjs from 'dayjs';
import duration from 'dayjs/plugin/duration';

import { addAccessKey } from '../../api/client.ts';

dayjs.extend(duration);

const { Paragraph } = Typography;

type AddAccessKeyProps = {
  isOpen: boolean;
  onSuccess: () => void;
  onCancel: () => void;
};

type FieldType = {
  friendlyName: string;
  ttl: number;
};

export const AddAccessKey: FC<AddAccessKeyProps> = ({
  isOpen,
  onSuccess,
  onCancel,
}) => {
  const [form] = Form.useForm<FieldType>();
  const { message } = App.useApp();
  const [key, setKey] = useState(null);

  const { mutate, isPending } = useMutation<
    AxiosResponse,
    AxiosError,
    { friendlyName: string; ttl: number }
  >({
    mutationFn: addAccessKey,
    onSuccess: (response) => {
      if (response?.data?.accessKey) {
        setKey(response?.data?.accessKey?.name);
      }
    },
    onError: (error) => {
      message.open({
        type: 'error',
        content:
          (error?.response?.data as string) || "Couldn't create new access key",
      });
    },
  });

  const onSubmit: FormProps<FieldType>['onFinish'] = ({
    friendlyName,
    ttl,
  }) => {
    const milliseconds = dayjs.duration(ttl || 60, 'days').asMilliseconds();
    mutate({
      friendlyName,
      ttl: milliseconds,
    });
  };

  const onFormCancel = () => {
    if (!key) {
      form.resetFields();
    }
    setKey(null);
    onSuccess();
    onCancel();
  };

  return (
    <Modal
      title="New access key"
      open={isOpen}
      okText={key ? 'Done' : 'Add new key'}
      onOk={() => {
        if (!key) {
          form.submit();
        } else {
          onFormCancel();
        }
      }}
      confirmLoading={isPending}
      onCancel={onFormCancel}
    >
      {key ? (
        <>
          <Paragraph>
            For security, the key will only be shown once on creation, so
            remember to save it somewhere if needed
          </Paragraph>
          <Paragraph copyable code>
            {key}
          </Paragraph>
        </>
      ) : (
        <Form form={form} layout="vertical" onFinish={onSubmit}>
          <Form.Item<FieldType>
            label="Name"
            name="friendlyName"
            rules={[
              {
                required: true,
                message: 'Please input your key name',
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label="TTL"
            name="ttl"
            initialValue={60}
            extra="By default, access keys expire in 60 days"
          >
            <InputNumber min={1} max={365} addonAfter="days" />
          </Form.Item>
        </Form>
      )}
    </Modal>
  );
};
